<template>
  <section class="checkFeeds pt-2 mx-3">
    <page-intro :introName="introName"></page-intro>
    <v-card>
      <v-card-title>
        <v-flex xs12>
          <v-text-field v-model="search"
                        prepend-icon="filter_list"
                        label="Filter results"
                        single-line
                        hide-details>
          </v-text-field>
        </v-flex>
      </v-card-title>
      <sqltable :search="search"
                :restoreResults="filteredResults"></sqltable>
    </v-card>
  </section>
</template>

<script>
import sqltable from "@/components/SQL/Table";
import { GetAllRestores } from "@/services/api/SQLRestores";
import PageIntro from "@/components/PageIntro.vue";

export default {
  components: {
    sqltable,
    PageIntro
  },

  data() {
    return {
      introName: "Restores Pending",
      search: "",
      restoreResults: []
    };
  },

  computed: {
    filteredResults() {
      let filter = this.restoreResults.filter(element => {
        return element.currentStatus !== "RestoreCompleted";
      });
      return filter;
    }
  },

  async mounted() {
    await GetAllRestores().then(data => {
      this.restoreResults = data;
    });
  }
};
</script>
